@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&family=Source+Code+Pro:ital,wght@0,200..900;1,200..900&display=swap');

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-new-logo {
  height: 6rem;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #0A0A0A;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1 {
  font-family: "Source Code Pro", monospace;
  font-weight: 600;
  font-style: normal;
}

/* === Registration Form Styling === */

.register-container {
  background-color: #D9D9D9;
  border-radius: 8px;
  padding: 2rem;
  width: 90%;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.register-title {
  font-size: 24px;
  font-weight: bold;
  color: #0D47A1;
  margin-bottom: 1rem;
}

.register-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  gap: 12px;
}

.form-input,
.form-textarea,
.form-button,
select {
  width: 100%;
  padding: 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 16px;
}

/* Ensure selects match input widths */
select.form-input {
  background-color: white;
  cursor: pointer;
}

/* Style first option (placeholder) */
select.form-input option:disabled {
  color: #4CAF50;
}

/* Fix select hover colors */
select.form-input option {
  background-color: white;
  color: black;
}

select.form-input option:hover {
  background-color: #ddd;
}

/* Ensure consistency */
.form-textarea {
  height: 100px;
  resize: none;
}

.form-button {
  background-color: #4CAF50;
  color: white;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.3s ease-in-out;
}

.form-button:hover {
  background-color: #3e8e41;
}

.register-message {
  margin-top: 10px;
  font-size: 14px;
  color: #B5DEB7;
}
